.custom-card-header {
    font-size: 18px;
    margin-bottom: 5px;
    padding: 4px;
    &__status {
        color: white;
        padding: 4px 10px;
        border-radius: 20px;
        width: max-content;
        font-size: 14px;
        margin-left: 4px;
        float: right;
        &.alive {
            background-color: green;
        }

        &.unknown {
            background-color: grey;
        }

        &.dead {
            background-color: red;
        }
    }
}